define([
    "backbone",
    "loglevel"
],function(Backbone, log){

    "use strict";

    var NotFoundView = Backbone.View.extend({

        render: function () {
            log.info("Rendering NotFound - Search");

            this.$el.html("<h1>Oh snap! 404</h1>");
            return this;
        },

        remove: function() {
            Backbone.View.prototype.remove.apply(this, arguments);
        }

    });

    return NotFoundView;

});


